.text-center {
  text-align: center;
}

h1 {
  text-align: center;
  margin: 0;
  font-weight: 600;
  font-size: 34px;
  line-height: 36px;
}

label {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  margin-bottom: 4px;
}

h2 {
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.15px;
  margin: 0px 0px 16px 0px;
}